<template>
  <div class="container">
    <page-title first-title="发布报名"></page-title>
    <div class="button-wrapper">
      <el-button type="primary" @click="AddNewtheme = true">去发布</el-button>
    </div>
    <div class="search-wrapper">
      <el-form :inline="true">
        <el-form-item label="报名主题：">
          <el-input placeholder="报名主题" clearable v-model="params.secName"></el-input>
        </el-form-item>
        <el-form-item>
          <el-button icon="el-icon-search" type="primary" @click="getList">
            搜索
          </el-button>
          <el-button @click="clear">
            清空
          </el-button>
        </el-form-item>
      </el-form>
    </div>
    <div class="list-wrapper">
      <el-table :data="list" stripe width="100%" :header-cell-style="{ background: '#eef1f6' }">
        <el-table-column type="index" label="序号"></el-table-column>
        <el-table-column prop="secName" label="报名主题"></el-table-column>
        <el-table-column label="支持类目" prop="categoryId"></el-table-column>
        <el-table-column prop="createdAt" label="发布时间" :formatter="timeStampFmt"></el-table-column>
        <el-table-column prop="startTime" label="报名时间" :formatter="timeStampFmt"></el-table-column>
        <el-table-column prop="endTime" label="结束时间" :formatter="timeStampFmt"></el-table-column>
        <el-table-column prop="signUpStatusStr" label="报名状态"></el-table-column>
        <el-table-column label="操作" min-width="200px">
          <template slot-scope="scope">
            <el-button
              type="text"
              size="small"
              @click="goEdit(scope.row)"
              v-if="scope.row.signUpStatus === 0 || scope.row.signUpStatus === 1"
              >编辑</el-button
            >
            <el-button type="text" size="small" @click="goRemove(scope.row.id)" v-if="scope.row.signUpStatus === 0"
              >移除</el-button
            >
          </template>
        </el-table-column>
      </el-table>
    </div>

    <!-- 分页 -->
    <el-pagination
      @current-change="handleCurrentChange"
      :current-page.sync="currentPage"
      background
      layout="prev, pager, next, jumper"
      :total="total"
    >
    </el-pagination>

    <!-- 编辑 -->
    <el-dialog title="秒杀报名活动编辑" :visible.sync="editheme">
      <div class="edit">
        <el-form :model="form" label-width="100px">
          <el-form-item label="报名主题：">
            <el-input v-model="form.secName" disabled></el-input>
          </el-form-item>
          <el-form-item label="支持类目：">
            <el-input v-model="form.categoryId" disabled></el-input>
          </el-form-item>
          <el-form-item label="报名时间：">
            <el-col :span="11">
              <el-form-item>
                <el-date-picker v-model="form.startTime * 1000" type="datetime" disabled></el-date-picker>
              </el-form-item>
            </el-col>
            <el-col class="line" :span="2" style="text-align: center;">至</el-col>
            <el-col :span="11">
              <el-form-item>
                <el-date-picker v-model="form.endTime * 1000" type="datetime" disabled></el-date-picker>
              </el-form-item>
            </el-col>
          </el-form-item>
          <el-form-item label="主题说明：">
            <el-input v-model="form.secDesc" type="textarea" rows="4" show-word-limit maxlength="300"></el-input>
          </el-form-item>
        </el-form>
      </div>
      <span slot="footer" class="dialog-footer">
        <el-button @click="editheme = false">取 消</el-button>
        <el-button type="primary" @click="submitEdit">确 定</el-button>
      </span>
    </el-dialog>

    <!-- 新增 -->
    <el-dialog title="发布报名" :visible.sync="AddNewtheme">
      <div class="edit">
        <el-form :model="Newtheme" ref="Newtheme" :rules="rules" label-width="100px">
          <el-form-item label="报名主题：" prop="secName">
            <el-input v-model="Newtheme.secName" maxlength="30"></el-input>
          </el-form-item>
          <el-form-item label="支持类目：" prop="categoryId">
            <el-select
              v-model="Newtheme.categoryId"
              multiple
              filterable
              clearable
              @change="selected"
              placeholder="请选择"
              style="display:block"
            >
              <el-option v-for="item in options" :key="item.value" :label="item.label" :value="item.value"> </el-option>
            </el-select>
          </el-form-item>
          <el-form-item label="报名时间：" required>
            <el-col :span="11">
              <el-form-item prop="startTime">
                <el-date-picker
                  type="datetime"
                  placeholder="年-月-日 时:分"
                  v-model="Newtheme.startTime"
                  format="yyyy-MM-dd HH:mm"
                  value-format="timestamp"
                  style="width: 100%;"
                  :picker-options="pickerOptions0"
                ></el-date-picker>
              </el-form-item>
            </el-col>
            <el-col class="line" :span="2" style="text-align: center;">-</el-col>
            <el-col :span="11">
              <el-form-item prop="endTime">
                <el-date-picker
                  type="datetime"
                  value-format="timestamp"
                  placeholder="年-月-日 时:分"
                  v-model="Newtheme.endTime"
                  format="yyyy-MM-dd HH:mm"
                  style="width: 100%;"
                  :picker-options="pickerOptions1"
                  default-time="23:59:59"
                  @change="formatendtime"
                ></el-date-picker>
              </el-form-item>
            </el-col>
          </el-form-item>
          <el-form-item label="主题说明：" prop="type">
            <el-input
              v-model="Newtheme.secDesc"
              type="textarea"
              rows="4"
              maxlength="300"
              placeholder="300字符以内"
              show-word-limit
            ></el-input>
          </el-form-item>
        </el-form>
      </div>
      <span slot="footer" class="dialog-footer">
        <el-button type="primary" @click="submitAdd('Newtheme')">发 布</el-button>
      </span>
    </el-dialog>
  </div>
</template>

<script>
/**
 * 时间格式化方法：时间戳，后台到s级别的，前端获取到的为ms
 * 状态名称转换方法
 */

export default {
  name: 'addNewEnroll',
  data() {
    return {
      pickerOptions0: {
        disabledDate: time => {
          if (this.Newtheme.endTime != '' && this.Newtheme.endTime != null) {
            return time.getTime() < Date.now() - 3600 * 1000 * 24 * 1 || time > this.Newtheme.endTime
          } else {
            return time.getTime() < Date.now() - 3600 * 1000 * 24 * 1
          }
        }
      },
      pickerOptions1: {
        disabledDate: time => {
          return time < this.Newtheme.startTime - 3600 * 1000 * 24 * 1
        }
      },
      params: {
        page: 1,
        limit: 10,
        secName: '',
        type: '2'
      },
      currentPage: 1,
      total: 0,
      list: [],
      loading: null,
      editheme: false,
      AddNewtheme: false,
      form: {},
      Newtheme: { secName: '', categoryId: '', startTime: '', endTime: '', secDesc: '' },
      options: [
        {
          value: '1',
          label: '黄金糕'
        }
      ],
      rules: {
        secName: [
          { required: true, message: '请输入活动名称', trigger: 'blur' },
          { min: 1, max: 30, message: '长度在 1 到 30 个字符', trigger: 'blur' }
        ],
        categoryId: [{ required: true, message: '支持类目', trigger: 'change' }],
        startTime: [{ type: 'date', required: true, message: '请选择开始时间', trigger: 'change' }],
        endTime: [{ type: 'date', required: true, message: '请选择结束时间', trigger: 'change' }],
        secDesc: [{ required: true, message: '请填写活动形式', trigger: 'blur' }]
      },
      allowable: true,
      id: ''
    }
  },
  created() {
    this.getList()
    this.gettypeList()
  },
  methods: {
    // 列表
    getList() {
      this.loading = true
      this.$api.get(`hd/itzSecbuySignup/signup`, this.params, r => {
        console.log(r)
        if (r.status == 200) {
          this.list = r.data.itzSecbuySignupVoList
          this.total = r.data.total
          console.log(this.list)
        } else {
          this.list = {}
          this.$message.error(r.message)
        }
      })
    },
    // 获取商品一级类目
    gettypeList() {
      this.$api.get('hd/itzSecbuySignup/categoryList', null, r => {
        console.log(r)
        if (r.status == 200) {
          this.options = r.data
        }
      })
    },
    clear() {
      this.params.page = 1
      this.params.theme = ''
      this.params.secName = ''
      this.getList()
    },
    goEdit(row) {
      this.editheme = true
      this.id = row.id
      this.form = row
      console.log(this.form)
    },
    // 页面切换
    handleCurrentChange() {
      this.params.page = this.currentPage
      this.getList()
    },
    // 格式化时间戳
    timeStampFmt(row, column) {
      let key = typeof column === 'string' ? column : column.property
      let _t = row[key]
      return _t ? this.utils.formatDate('YYYY-MM-DD HH:mm:ss', this.utils.formatStamp(_t, 1)) : _t
    },
    formatendtime(value) {
      if (value) {
        if (value < this.Newtheme.startTime) {
          this.$alert('结束时间不能早于开始时间', '提示', {
            confirmButtonText: '确定',
            callback: () => {
              this.Newtheme.endTime = ''
            }
          })
          return
        }
      }
    },
    submitAdd(formName) {
      this.$refs[formName].validate(valid => {
        if (valid && this.allowable) {
          this.allowable = false
          this.Newtheme.categoryId = this.Newtheme.categoryId.join(',')
          // this.utils.formatStamp(this.Newtheme.startTime)
          // this.utils.formatStamp(this.Newtheme.endTime)
          this.Newtheme.startTime = parseInt(this.Newtheme.startTime / 1000)
          this.Newtheme.endTime = parseInt(this.Newtheme.endTime / 1000)
          this.$api.post(`hd/itzSecbuySignup/signup`, this.Newtheme, r => {
            if (r.status == 200) {
              // console.log(r)
              this.AddNewtheme = false
              this.Newtheme = {}
              this.$refs[formName].resetFields()
              this.getList()
            }
          })
        } else {
          this.allowable = true
          console.log('error submit!!')
          return false
        }
      })
    },
    submitEdit() {
      console.log(this.form.secDesc)
      this.$api.put(`hd/itzSecbuySignup/signup/${this.id}`, this.form, r => {
        console.log(r)
        if (r.status == 200) {
          this.editheme = false
          this.getList()
        } else {
          this.$message({
            type: 'error',
            message: r.message
          })
        }
      })
    },
    goRemove(id) {
      this.$confirm('此操作将移除该活动, 是否继续?', '提示', {
        confirmButtonText: '确定',
        cancelButtonText: '取消',
        type: 'warning'
      })
        .then(() => {
          this.$api.delete(`hd/itzSecbuySignup/signup/${id}`, null, r => {
            this.$message({
              type: 'success',
              message: '删除成功!'
            })
            this.getList()
          })
        })
        .catch(() => {
          this.$message({
            type: 'info',
            message: '已取消删除'
          })
        })
    },
    selected(val) {
      console.log(val)
      this.Newtheme.categoryId
    }
  }
}
</script>

<style lang="scss" scoped>
.header {
  margin-bottom: 10px;
}
.button-wrapper {
  text-align: right;
  margin-bottom: 10px;
}
.search-wrapper {
  padding: 10px 10px 0;
  margin-bottom: 10px;
  background: #fff;
  border: 1px solid #eee;
  .el-form-item {
    margin-bottom: 10px;
  }
}
.el-pagination {
  margin-top: 10px;
  text-align: center;
}
.edit {
  width: 90%;
}
.el-date-editor.el-input {
  width: 100%;
}
</style>
